<template>
  <MyForm
    :form="form"
    ref="customerSearchForm"
    :formData="formData"
    :formItem="formItem"
    @submit="searchSubmit"
    @resetForm="reset"
  ></MyForm>
</template>
<script>
import { getFormField } from '../../getField'

import MyForm from '@/components/MyForm'

export default {
  data() {
    // 表单配置
    var customerListForm = {
      showFormBorder: false,
      showTitle: false,
      ref: 'customerSearchForm',
      labelWidth: '120px',
      labelPositon: 'right',
      style: 'notprepend',
      column: 3,
      btnStyle: 'inline',
      hasSubmit: true,
      btnRight: true,
      cancleShow: true,
      submitText: '搜索',
      cancleText: '重置'
    }
    return {
      searchParams: {},
      formItem: [],
      formData: {},
      form: customerListForm
    }
  },
  props: {
    optionsVal: {
      type: String,
      default: ''
    },
    moduleName: {
      type: String,
      default: ''
    }
  },
  components: {
    MyForm
  },
  created() {
    this.formInit()
  },
  methods: {
    searchSubmit(val) {
      this.$emit('searchFun', val)
    },
    formInit(row) {
      this.formItem = getFormField('healthFileTableSearch', 'item')
      this.formData = row ? row : getFormField('healthFileTableSearch', 'data')
    },
    reset() {
      this.$emit('searchFun')
    }
  }
}
</script>
