<template>
  <div class="customer__box">
    <el-row class="margin-top-20">
      <searchForm @searchFun="searchFun"></searchForm>
    </el-row>
    <FileTable
      :searchParams="searchParams"
      @searchFlag="setSearchFlag"
      :searchFlag="searchFlag"
      :serviceItem="serviceItem"
      :radioColumn="radioColumn"
      @rowClick="rowClick"
      :showOperator="showOperator"
    >
    </FileTable>
  </div>
</template>
<script>
import searchForm from './SearchForm'
import FileTable from './FileTable'

export default {
  data() {
    return {
      searchParams: {},
      searchFlag: false
    }
  },
  components: {
    searchForm,
    FileTable
  },
  props: {
    radioColumn: {
      type: Boolean,
      default: false
    },
    showOperator: {
      type: Boolean,
      default: false
    },
    serviceItem: {
      type: String,
      default: 'health_record'
    }
  },
  created() {},
  methods: {
    setSearchFlag(val) {
      this.searchFlag = val
    },
    rowClick(row) {
      this.$emit('rowClick', row)
    },
    refreshRelated() {
      this.searchParams = this.searchParams ? this.searchParams : {}
      this.setSearchFlag(true)
    },
    searchFun(val) {
      this.searchParams = val ? val : {}
      this.setSearchFlag(true)
    }
  }
}
</script>

<style lang="scss">
.customer__box {
  .step-box {
    margin: 0 auto;
    width: 48%;
  }
}
</style>
