<template>
  <div v-loading="loading">
    <MyTable
      size="mini"
      :title="'服务中的患者'"
      :radioColumn="radioColumn"
      :titleShow="true"
      :stripe="false"
      :border="true"
      :multiple="false"
      :hasIndex="false"
      :outlineBorder="false"
      :operation="operation"
      :column="column"
      :data="tableData"
      :currentPage="currentPage"
      :pageSize="pageSize"
      :total="total"
      @fileDetail="fileDetail"
      @rowClick="rowClick"
      @handleSizeChange="handleSizeChange"
      @handleCurrentChange="handleCurrentChange"
    >
      <template #member="scope">
        <div>
          <div>
            {{ scope.row.name
            }}<svg-icon v-if="scope.row.is_replace" icon-class="replace" />
          </div>
          <div>{{ scope.row.babyId }}</div>
        </div>
      </template>
      <template #userInfo="scope">
        <div>
          <div class="user-avatar">
            <el-avatar
              :key="scope.row.user_info._id"
              :src="scope.row.user_info.avatar"
              :size="28"
            />
            {{ scope.row.user }}
          </div>
          <div>{{ scope.row.user_info.user_id }}</div>
          <p>
            {{ scope.row.user_info.nickname }}
          </p>
        </div>
      </template>
      <template #userStage="scope">
        {{ curStageTxt(scope.row.chanke.cur_stage) }}
      </template>
      <template #userRemark="scope">
        {{
          scope.row.user_info.remark && scope.row.user_info.remark.content
            ? scope.row.user_info.remark.content
            : '-'
        }}
      </template>
      <template #servicePackage="scope">
        {{ scope.row.fd_service_type_info.name }}
      </template>
      <template #serviceTime="scope">
        <p>
          {{
            formatTime(scope.row.fd_contract_info.started_at, '{y}-{m}-{d}')
          }}到
        </p>
        {{ formatTime(scope.row.fd_contract_info.ended_at, '{y}-{m}-{d}') }}
      </template>
    </MyTable>
  </div>
</template>
<script>
import { getField } from '../../getField'
import { curStageTxt, gender } from '@/filters'

import MyTable from '@/components/MyTable'

import { getOHealthFileList } from '@/api/healthManagement'

export default {
  data() {
    // 表格操作配置
    var operation = {
      nowPage: 'relatedTable',
      show: true,
      fixed: false,
      size: 'mini',
      width: 80,
      minWidth: 100,
      label: '操作',
      btns: [
        {
          type: 'text',
          size: 'mini',
          content: '档案详情',
          icon: '',
          handle: 'fileDetail',
          class: 'button-operator'
        }
      ]
    }
    return {
      userId: '',
      column: [],
      loading: false,
      formItem: [],
      formData: {},
      tableData: [],
      pageSize: 10,
      currentPage: 1,
      total: 0,
      operation: operation // 操作按钮
    }
  },
  components: {
    MyTable
  },

  props: {
    searchParams: {
      type: Object,
      default: () => {}
    },
    searchFlag: {
      type: Boolean,
      default: false
    },
    radioColumn: {
      type: Boolean,
      default: false
    },
    showOperator: {
      type: Boolean,
      default: false
    },
    serviceItem: {
      type: String,
      default: 'health_record'
    }
  },
  watch: {
    searchFlag(val) {
      if (val === true) {
        this.getHealthFileList()
      }
    },
    showOperator() {
      console.log('是否显示按钮', this.showOperator)
      this.operation.show = this.showOperator
    }
  },
  created() {
    this.operation.show = this.showOperator
    this.fieldInit()
    this.getHealthFileList()
  },
  methods: {
    curStageTxt,
    gender,
    rowClick(row) {
      this.$emit('rowClick', row)
    },
    fieldInit() {
      // 获取字段
      var column = getField('healthFileList')
      column.forEach(item => {
        if (!!item.width && item.width != 'auto') {
          item.width = '300'
        }
      })
      this.column = column
    },
    // 档案详情
    fileDetail(row) {
      this.$router.push({
        path: `/obstetricsDoctor/healthFile/detail/${row._id}`,
        query: {
          userId: this.$route.query.userId
        }
      })
    },
    // 分页切换
    handleCurrentChange(index) {
      this.currentPage = index
      this.getHealthFileList()
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getHealthFileList()
    },
    setTableData({ list }) {
      this.tableData = list
    },
    getHealthFileList() {
      const pageAttr = {
        current_page: this.currentPage,
        page_size: this.pageSize,
        type: 'chanke',
        service_item: this.serviceItem,
        show_replace: 1
      }
      this.loading = true
      const params = Object.assign({}, pageAttr, this.searchParams)
      getOHealthFileList(params)
        .then(res => {
          this.$emit('searchFlag', false)
          this.loading = false
          if (res) {
            this.total = res.data.count
            console.log('档案列表', res.data)
            this.setTableData(res.data)
          }
        })
        .catch(error => {
          this.loading = false
        })
    }
  }
}
</script>
